import React from 'react';
import { Result, Button } from 'antd';
import classes from './ResultComponent.module.less';

const ResultComponent = ({totalScore, initializeNewGame, makeFullEnd}) => {
    if (totalScore >= 0 && totalScore < 20) {
        return (
            <Result
                status="success"
                title={` .   ${totalScore}   30 `}
                subTitle=",     .     ?"
                extra={[
                    <Button type="primary" key="console" danger onClick={() => {initializeNewGame()}}>
            
                    </Button>,
                    <Button key="buy" onClick={() => {makeFullEnd()}}> </Button>,
                ]}
            />
        );
    }

    if (totalScore >= 20 && totalScore < 30) {
        return (
            <Result
                status="success"
                title={` .   ${totalScore}   30 `}
                subTitle="   ?"
                extra={[
                    <Button type="primary" key="console" danger onClick={() => {initializeNewGame()}}>
            
                    </Button>,
                    <Button key="buy" onClick={() => {makeFullEnd()}}> </Button>,
                ]}
            />
        );
    }

    if (totalScore === 30) {
        return (
            <Result
                status="success"
                title={`    ${totalScore} .    .`}
                subTitle=",      .      .   -   ?"
                extra={[
                    <Button type="primary" key="console" danger onClick={() => {initializeNewGame()}}>
            
                    </Button>,
                    <Button key="buy" onClick={() => {makeFullEnd()}}> </Button>,
                ]}
            />
        );
    }


};

export default ResultComponent;